
<script>
import LeadDataService from "../../../services/DataService";
import DynamicForm from "../../../components/Common/DynamicForm.vue";
import modalpricebook from '../../../components/Common/Modal/modalpricebook.vue';
import $ from "jquery";
var ajaxRequestComplete = true;

export default {
    components: {
        DynamicForm,
        modalpricebook
    },
    data() {
        return {
            value: "",
            LeadData: null,
            LeadOwner: null,
            GroupBy: [],
            Channel: null,
            IndustryType: null,
            Status: null,
            quoteID: this.$route.params.id,
            reqFromId: this.$route.params.reqFromId,
            currency:null,
            options: {              
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                maxDate: new Date(Date.now())
            },
            DependentOption: [{               
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date(Date.now())
            }
            ],
            selected: false,
            selectoptions: [],
            Address: null,
            ProductSummary: null,
            CountryData: [],
            AccountData: [],
            ShippingContactPerson: [],
            showPricebookModal: false,
            selectedItemIndex: null,
            AccountId: 0,
            statusId: 0,
            submited: false,
            isTaxNameFilled: false,
            vSelectValidate: false,
            vSelectContactPerson: false,
            sameShippingAddressAsBilling: false,
            qlist: '',
            redirectURL: '',
            selectedQuotationType: '0',
            Ischeckshipping: false,
            IsInventory: true,
            accntid: 0,
            dealid: 0,
            odnumber: '',
            dateformat:null,
            userId:null,
            companyId:null,
            isLoading:false,
        }
    },
    created: function () {
        this.userId= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.dateformat= this.GetUserInfo.DateFormat;
        this.currency=this.GetUserInfo.Currency;
        if(this.currency){
            this.currency=(this.currency == "(USD)" ? "$" : this.currency);
        }
        Number.prototype.toFixed2 = function () {
            var amount = Math.floor(parseFloat(this, 10) * 100) / 100;
            return parseFloat(amount, 10).toFixed(2);
        };
        this.FetchData();
        this.GetCountryData('', 'COUNTRIES');
        this.qlist = this.$route.params.reqFromId;
        this.redirectURL = (decodeURIComponent(this.qlist)).split("&amp;").join("&");
    },
    // directives: {
    //     'click-outside': {
    //         bind: function (el, binding, vNode) {
              
    //             if (typeof binding.value !== 'function') {
    //                 const compName = vNode.context.name
    //                 let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
    //                 if (compName) { warn += `Found in component '${compName}'` }
    //                 console.warn(warn)
    //             }
               
    //             const bubble = binding.modifiers.bubble
    //             const handler = (e) => {
    //                 if (bubble || (!el.contains(e.target) && el !== e.target)) {
    //                     binding.value(e)
    //                 }
    //             }
    //             el.__vueClickOutside__ = handler
              
    //             document.addEventListener('click', handler)
    //         },
    //         unbind: function (el, binding) {
             
    //             document.removeEventListener('click', el.__vueClickOutside__)
    //             el.__vueClickOutside__ = null
    //         }
    //     }
    // },
    methods: {
        getQuantityValidationRule(item){             
            var initialRule={}
            initialRule.required=true;
            initialRule.numeric=true;
            initialRule.min_value=1;
            if(this.selectedQuotationType == 2){
                initialRule.max_value=item.ProductInStock?item.ProductInStock==0?initialRule.min_value:item.ProductInStock:initialRule.min_value;
            }
            return initialRule;
        },
        onCancel: function () {
            if(this.reqFromId!="0"){
                this.$router.push({path:`/CRM/ViewDealDetails/${this.reqFromId}/CRM/CRM_DEALS`});
            }else{
                this.$router.push({ name: 'CRMQuote' });
            }
        },
        encodeHtmlCode: function (value) {
            if (!Array.isArray(value)) {
                value = value.replace("\"", "\\\"");
            }
            return value;
        },
        decodeHtmlCode: function (value) {
            if (!Array.isArray(value)) {
                value = unescape(value);
            }
            return value;
        },
        ClearAll: function (item) {
            item.ProductQuantity = '';
            this.ProductSummary.ShippingCharges = '';
            this.ProductSummary.Adjustments = '';
            this.ProductSummary.SubTotal = '';
            this.ProductSummary.SummaryTotal = '';
        },
        checkIfQuoteTypeSelected: function (item) {
            var vm=this;
            if (this.selectedQuotationType == '0') {
                vm.ShowAlert("Please select Quotation type first.", "warning", true,this.$t("Alert"))
                item.ProductName = "";
            }           
        },
        isNumber: function (evt) {
           var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();
            } else {
                return true;
            }
        },
        chkapplicableshipping: function (e) {
            this.Ischeckshipping=$(e.target).prop("checked");
            if (!this.Ischeckshipping) {
                this.Address.ShippingAddress.ShippingAccountCity = "";
                this.Address.ShippingAddress.ShippingAccountAddress = "";
                this.Address.ShippingAddress.ShippingZipCode = "";
                this.Address.ShippingAddress.ShippingAccountAddress2 = "";
                this.Address.ShippingAddress.ShippingState = "";
                this.Address.ShippingAddress.ShippingContactPerson = "";
                this.Address.ShippingAddress.ShippingCountry = "";
                this.Address.ShippingAddress.ShippingCompanyName = "";
                this.sameShippingAddressAsBilling = false;
            }
            else{
                $("#chkCloneBillingAddress").attr("checked", false);
                this.sameShippingAddressAsBilling = false;
            }
        },
        checkForSameBillingAndShippingAddress: function () {
            if (((typeof this.Address.ShippingAddress.ShippingContactPerson != 'undefined') && this.Address.ShippingAddress.ShippingContactPerson && this.Address.ShippingAddress.ShippingContactPerson.length > 0 && this.Address.ShippingAddress.ShippingContactPerson[0].value == this.Address.BillingAddress.BillingContactPerson[0].value) &&
                this.Address.ShippingAddress.ShippingAccountAddress == this.Address.BillingAddress.BillingAccountAddress &&
                this.Address.ShippingAddress.ShippingCity == this.Address.BillingAddress.BillingCity &&
                this.Address.ShippingAddress.ShippingZipCode == this.Address.BillingAddress.BillingZipCode &&
                this.Address.ShippingAddress.ShippingState == this.Address.BillingAddress.BillingState &&
                this.Address.ShippingAddress.ShippingAccountAddress2 == this.Address.BillingAddress.BillingAccountAddress2 &&
                this.Address.ShippingAddress.ShippingAccountCity == this.Address.BillingAddress.BillingAccountCity
                && this.Address.ShippingAddress.ShippingCompanyName == this.Address.BillingAddress.BillingCompanyName
                && this.Address.ShippingAddress.ShippingAccountAddress2 == this.Address.BillingAddress.BillingAccountAddress2) {
                this.sameShippingAddressAsBilling = true;
                this.Ischeckshipping = true;
            }
        },
        clearRateIfNameEmpty: function (item, type, isInPercentage) {
            if ($.trim(item.TAX_NAME) == '') {
                item.TAX_RATE = 0;
                item.TAX_NAME = $.trim(item.TAX_NAME)
            } else {
                this.isTaxNameFilled = false;
            }
        },
        outside: function (e) {
            // $.each(this.ProductSummary.SummaryData, function (key, item) {
            //     item.toggleTax = false;
            //     item.toggleDiscount = false;
            // });
        },
        onlyDecimal: function (item, type, isInPercentage) {
            if (type == 'TAX') {
                if (item.TAX_NAME != '') {
                    item.TAX_RATE = item.TAX_RATE.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                    if (isInPercentage && parseFloat((item.TAX_RATE != '') ? item.TAX_RATE : 0) > 100) {
                        item.TAX_RATE = '100';
                    }
                } else {
                    this.isTaxNameFilled = true;
                    item.TAX_RATE = "0";
                }
            } else if (type == 'DISCOUNT') {
                item.ProductDiscountValue = item.ProductDiscountValue.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                if (isInPercentage && parseFloat((item.ProductDiscountValue != '') ? item.ProductDiscountValue : 0) > 100) {
                    item.ProductDiscountValue = '100';
                }

            }
            else if (type == 'Adjustments') {
                if (item.Adjustments.charAt(0) == '.') {
                    item.Adjustments = ('0' + item.Adjustments);
                }
            }
            else if (type == 'Shipping') {
                if (item.ShippingCharges.charAt(0) == '.') {
                    item.ShippingCharges = ('0' + item.ShippingCharges);
                }
            }
        },
        DependentDateObject: function (item, date, callType) {
            var Quoteid = this.Quoteid;
            if (typeof (Quoteid) == 'undefined' || Quoteid == "")
                Quoteid = 0;
            if (item.name == 'order_date') {
                var checkDate = new Date();
                if ((typeof date !== 'undefined') && date != '') {
                    checkDate = new Date(Date.parse(date));
                }
                else {
                    checkDate = item.value;
                }
                this.DependentOption[0] = {
                    
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: checkDate
                }
                $.each(this.GroupBy, function (filedIndex, fieldItem) {
                    $.each(fieldItem.Data, function (index, element) {
                        if (element.name == "order_expiry_date" && callType != 'fetchCall') {
                            element.value = '';
                        }
                    });
                });
            }
        },
        showDependentItem: function (item, option_value) {
            
            var vm = this;
            var setValue = 0;
            if (option_value == 2) {
                var setValue = 1;
                vm.selectedQuotationType = option_value;
            }
            else if (option_value == 1) {
                var setValue = 1;
                vm.selectedQuotationType = option_value;
            }
            else {
                vm.selectedQuotationType = option_value;
            }
            $.each(vm.GroupBy, $.proxy(function (key, parentItem) {
                $.each(parentItem.Data, function (childKey, childItem) {
                    if (childItem.parent_id == item.custom_field_id && childItem.dependent_type == 'show') {
                        childItem.dependent_show_type = 0;
                        if (option_value == 2 && childItem.name == "deal_id") {
                            childItem.dependent_show_type = setValue;
                        } else if (option_value == 1 && childItem.name == "client_id") {
                            childItem.dependent_show_type = setValue;
                        }
                    }
                });
            }, vm));
            item.value = option_value;
            var productSummary = [{
                ProductName: '',
                ProductList: [],
                ProductDescription: '',
                ProductQuantity: 0,
                ProductInStock: 0,
                ProductRate: 0,
                ProductTax: 0,
                ProductAmount: 0,
                ProductDiscount: 0,
                ProductDiscountType: 'percent',
                ProductDiscountValue: 0,
                ProductItemTotal: 0,
                PriceBookId: 0,
                toggleDiscount: false,
                toggleTax: false,
                TaxData: [],
                DiscountData: [],
            }];
            vm.ProductSummary = {
                SubTotal: 0,
                ShippingCharges: 0,
                Adjustments: 0,
                RoundOff: 0,
                SummaryTotal: 0,
                SummaryData: productSummary
            }
        },
        onKeyDown: function () {
            ajaxRequestComplete = true;
        },
        onSearch: function onSearch(search, objItem, isFor) {     
            if(search)     {
                this.searchSelectData(search, this, objItem, isFor);
            } 
            
        },
        searchSelectData: function (search, vm, objItem, isFor) {
            this.ShowSearchData(search, vm, objItem, isFor);
        },
        ShowSearchData: function (search, vm, objItem, isFor) {
            var url = "";            
            if (isFor == "ITEM_NAME_SUMMARY") {
                url = `id=&moduleName=${vm.$route.params.moduleName}&type=ITEM_NAME`;
            }
            else {
                var refId = 0;
                if (vm.AccountId > 0 && objItem.field_code != "ACCOUNTS") {
                    refId = vm.AccountId;
                }
                
                url = `id=&moduleName=${vm.$route.params.moduleName}&type=${objItem.field_code}&refId=${refId}`;
            }
            LeadDataService.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    vm.productLoading=false;
                    var json = [];
                    if (response.data != null) {
                        json = response.data;
                    }

                    if (isFor == "ITEM_NAME_SUMMARY") {
                        $.each(vm.ProductSummary.SummaryData, function (key, item) {
                            json.DATA = json.DATA.filter(function (el) {
                                return (el.value == item.ProductName.value) ? false : true;
                            });
                        });
                        objItem.ProductList = json.DATA;
                    }
                    else {
                        objItem.select_options = json.DATA;
                    }
                });
        },
        GetAddrData: function (addrid) {
            var vm = this;
            addrid = vm.Address.BillingAddress.BillingCompanyName;
            if (typeof (addrid) == 'undefined') {
                addrid = 0;
            }
            var AddrUrl = "addressid=" + addrid + "&sourceid=" + vm.accntid;
            LeadDataService.GetAddressdata(AddrUrl).then(response => {
                var Addrdata = response.data.DATA;
                vm.Address.BillingAddress.BillingAccountAddress = Addrdata[0].ACCOUNT_ADDRESS == null ? "" : Addrdata[0].ACCOUNT_ADDRESS;
                vm.Address.BillingAddress.BillingAccountAddress2 = Addrdata[0].ACCOUNT_ADDRESS2 == null ? "" : Addrdata[0].ACCOUNT_ADDRESS2;
                vm.Address.BillingAddress.BillingAccountCity = Addrdata[0].ACCOUNT_CITY == null ? "" : Addrdata[0].ACCOUNT_CITY;
                vm.Address.BillingAddress.BillingZipCode = Addrdata[0].ACCOUNT_ZIP_CODE == null ? "" : Addrdata[0].ACCOUNT_ZIP_CODE;
                vm.Address.BillingAddress.BillingCountry = Addrdata[0].ACCOUNT_COUNTRY == null ? "" : Addrdata[0].ACCOUNT_COUNTRY;
                vm.Address.BillingAddress.BillingState = Addrdata[0].ACCOUNT_STATE == null ? "" : Addrdata[0].ACCOUNT_STATE;
                vm.Address.BillingAddress.BillingContactPerson = Addrdata[0].CONTACT_ID;
                vm.Address.BillingAddress.BillingCompanyName = Addrdata[0].ADDRESS_NAME == null || typeof (Addrdata[0].ADDRESS_NAME) == 'undefined' ? 0 : Addrdata[0].ADDRESS_NAME;
                if (vm.sameShippingAddressAsBilling == 1) {
                    vm.Address.ShippingAddress.ShippingAccountAddress = Addrdata[0].ACCOUNT_ADDRESS == null ? "" : Addrdata[0].ACCOUNT_ADDRESS;
                    vm.Address.ShippingAddress.ShippingAccountAddress2 = Addrdata[0].ACCOUNT_ADDRESS2 == null ? "" : Addrdata[0].ACCOUNT_ADDRESS2;
                    vm.Address.ShippingAddress.ShippingAccountCity = Addrdata[0].ACCOUNT_CITY == null ? "" : Addrdata[0].ACCOUNT_CITY;
                    vm.Address.ShippingAddress.ShippingZipCode = Addrdata[0].ACCOUNT_ZIP_CODE == null ? "" : Addrdata[0].ACCOUNT_ZIP_CODE;
                    vm.Address.ShippingAddress.ShippingCountry = Addrdata[0].ACCOUNT_COUNTRY == null ? "" : Addrdata[0].ACCOUNT_COUNTRY;
                    vm.Address.ShippingAddress.ShippingState = Addrdata[0].ACCOUNT_STATE == null ? "" : Addrdata[0].ACCOUNT_STATE;
                    vm.Address.ShippingAddress.ShippingContactPerson = Addrdata[0].CONTACT_ID;
                    vm.Address.ShippingAddress.ShippingCompanyName = Addrdata[0].ADDRESS_NAME == null || typeof (Addrdata[0].ADDRESS_NAME) == 'undefined' ? 0 : Addrdata[0].ADDRESS_NAME;
                }
            });
        },
        GetAddrData2: function (addrid) {
            var vm = this;
            if (typeof (addrid) == 'undefined') {
                addrid = 0;
            }
            var AddrUrl = "addressid=" + addrid + "&sourceid=" + vm.accntid;
            LeadDataService.GetAddressdata(AddrUrl).then(response => {
                var Addrdata = response.data.DATA;
                vm.Address.ShippingAddress.ShippingAccountAddress = Addrdata[0].ACCOUNT_ADDRESS == null ? "" : Addrdata[0].ACCOUNT_ADDRESS;
                vm.Address.ShippingAddress.ShippingAccountAddress2 = Addrdata[0].ACCOUNT_ADDRESS2 == null ? "" : Addrdata[0].ACCOUNT_ADDRESS2;
                vm.Address.ShippingAddress.ShippingAccountCity = Addrdata[0].ACCOUNT_CITY == null ? "" : Addrdata[0].ACCOUNT_CITY;
                vm.Address.ShippingAddress.ShippingZipCode = Addrdata[0].ACCOUNT_ZIP_CODE == null ? "" : Addrdata[0].ACCOUNT_ZIP_CODE;
                vm.Address.ShippingAddress.ShippingCountry = Addrdata[0].ACCOUNT_COUNTRY == null ? "" : Addrdata[0].ACCOUNT_COUNTRY;
                vm.Address.ShippingAddress.ShippingState = Addrdata[0].ACCOUNT_STATE == null ? "" : Addrdata[0].ACCOUNT_STATE;
                vm.Address.ShippingAddress.ShippingCompanyName = Addrdata[0].ADDRESS_NAME == null || typeof (Addrdata[0].ADDRESS_NAME) == 'undefined' ? 0 : Addrdata[0].ADDRESS_NAME;
                vm.Address.ShippingAddress.ShippingContactPerson = Addrdata[0].CONTACT_ID;
            });
        },
        setSelectedValue(item, val, isFor, type) {
            var vm = this;            
            if (isFor == 'shipping') {
                var refId = val;
                if (isFor != 'search') {
                    refId = val.value;
                }
                var search = '';
                if (typeof (refId) != "undefined") {


                    var url = `id=&moduleName=${vm.$route.params.moduleName}&type=${type}&refId=${refId}`;

                    LeadDataService.getDDLData(url + "&search=" + escape(search)).then(response => {                        
                        var json = response.data;
                        if (json != null) {
                            vm.Address.ShippingAddress.ShippingContactPerson = '';
                            return this.ShippingContactPerson = json.DATA.map((item) => {
                                return { value: item.value, label: item.name };
                            });
                        }
                    });
                }
            }
            else if (isFor == "ITEM_NAME_SUMMARY") {
                item.ProductName = val;
                if (ajaxRequestComplete) {
                    if (val.first_value > 0) {
                        item.ProductInStock = val.first_value;
                    }
                    if (val.second_value > 0) {
                        item.ProductRate = val.second_value;
                    }
                    vm.ShowTaxItems(item);
                    vm.CalculateAmount(item);
                    item.PriceBookId = 0;
                }
            }
            else {
                try {
                   var selectedVal=val;
                    if (Array.isArray(val)) {
                        selectedVal = (typeof val[0] === 'object' && val[0] !== null) ? val[0].value : val[0];
                    }
                    else {
                        selectedVal =  val.value;
                        vm.accntid = selectedVal;
                    }
                    if (item.name == 'account_id' && selectedVal > 0 && vm.quoteID == 0) {
                        vm.AccountId = typeof selectedVal == 'object' ? selectedVal.value : selectedVal;
                        vm.FillDropdown('ACCOUNT_CONTACTS', vm.AccountId);
                        vm.FillDropdown('ACCOUNT_DEALS', vm.AccountId);
                        vm.FillDropdown('ACCOUNT_CLIENTS', vm.AccountId);
                        vm.FillDropdown('ADDRESS_NAMES', vm.AccountId);
                        var AddrUrl = `addressid=-1&sourceid=${vm.AccountId}`;
                        LeadDataService.GetAddressdata(AddrUrl).then(response1 => {
                            var Addrdata =response1.data.DATA;
                            console.log(Addrdata)
                            vm.Address.BillingAddress.BillingContactPerson = Addrdata[0].CONTACT_ID;
                            vm.Address.BillingAddress.BillingAccountAddress = Addrdata[0].ACCOUNT_ADDRESS == null ? "" : Addrdata[0].ACCOUNT_ADDRESS;
                            vm.Address.BillingAddress.BillingAccountAddress2 = Addrdata[0].ACCOUNT_ADDRESS2 == null ? "" : Addrdata[0].ACCOUNT_ADDRESS2;
                            vm.Address.BillingAddress.BillingAccountCity = Addrdata[0].ACCOUNT_CITY == null ? "" : Addrdata[0].ACCOUNT_CITY;
                            vm.Address.BillingAddress.BillingZipCode = Addrdata[0].ACCOUNT_ZIP_CODE == null ? "" : Addrdata[0].ACCOUNT_ZIP_CODE;
                            vm.Address.BillingAddress.BillingCountry = Addrdata[0].ACCOUNT_COUNTRY == null ? "" : Addrdata[0].ACCOUNT_COUNTRY;
                            vm.Address.BillingAddress.BillingState = Addrdata[0].ACCOUNT_STATE == null ? "" : Addrdata[0].ACCOUNT_STATE;
                            vm.Address.BillingAddress.BillingCompanyName = Addrdata[0].ADDRESS_NAME == null || typeof (Addrdata[0].ADDRESS_NAME) == 'undefined' ? 0 : Addrdata[0].ADDRESS_NAME;
                           
                     
                    });
                    
                    }
                    
                }
                catch (ex) {
                }
            }
        },
        formatDate(value) {
            if (value) {
                return moment(String(value), "YYYY-MM-DD").format(String(Globalize.culture().calendar.patterns.d).toUpperCase())
            }
        },
        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = this.$t(String(displayName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        SubmitData: function (valid,errors,statusType) {
            var chkSummaryData = "";
            var chkSummaryData = "";
            var vm = this;
            var clientElement=  document.getElementById("client_id");
            var clientStyle = clientElement.computedStyleMap().get('display');
            var dealElement=  document.getElementById("deal_id");
            var dealStyle = dealElement.computedStyleMap().get('display');            
            var isDealVisible=dealStyle?dealStyle.value=='none'?false:true:false;
            var isClientVisible=clientStyle?clientStyle.value=='none'?false:true:false;
            vm.$refs.form.validate().then((result) => {
                vm.vSelectValidate = true; 
                  if(isClientVisible==false){
                    vm.$refs.form.errors['Client Name']=[];   
                  }
                  if(isDealVisible==false){
                    vm.$refs.form.errors['Deal']=[];   
                  } 
                  var finalRes=[];                  
                  Object.keys(vm.$refs.form.errors).forEach(rule => {
                    finalRes.push(vm.$refs.form.errors[rule].length);                   
                  })
                  result=finalRes.some(x=>x>0)?false:true;
                if (result) {
                    vm.isLoading=true;
                    vm.submited = true;
                    var fullJson = vm.LeadData;
                    var data = [];
                    var validation = [];
                    var quotationType = 0;
                    $.each(fullJson, function (key, item) {
                        var validationObject = {
                            field_name: item.name,
                            required: item.is_required,
                            length: item.length,
                            type: item.validation_type
                        };
                        var fieldValue = (item.name == "order_id" ? vm.quoteID : ((typeof item.value === "undefined") ? '' : item.value));
                        fieldValue = typeof fieldValue === "undefined" ? '' : fieldValue;
                        if (item.dt_code == "date") {
                            fieldValue = item.value;
                        }
                        if (item.name == 'quotation_type') {
                            quotationType = item.value;
                        }
                        if (item.dt_code == "checkbox") {
                            var filtered = item.select_value.filter(function (el) {
                                return (el != null && el != "");
                            });
                            fieldValue = filtered.join(',');
                        }
                        if (item.dt_code == "select") {
                            try {
                                fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue;
                            } catch (ex) { }
                        }
                        if (item.name == 'created_at') {                            
                            fieldValue = '';
                        }
                        if (item.name == 'created_by') {                            
                            fieldValue = vm.userId;
                        }
                        if (item.name == 'modified_by') {
                            fieldValue = vm.userId;
                        }
                        if (item.name == 'company_id') {
                            fieldValue = vm.companyId;
                        }
                        var dataObject = {
                            field_name: item.name,
                            field_value: fieldValue,
                            custom_field_id: item.custom_field_id
                        };
                        data.push(dataObject);
                        validation.push(validationObject);
                    });
                    var productSummaryData = [];
                    $.each(vm.ProductSummary.SummaryData, function (key, item) {
                        var projectId = 0;
                        if (quotationType == 2) {
                            if (Array.isArray(item.ProductName)) {
                                try {
                                    projectId = (typeof item.ProductName[0] === 'object' && item.ProductName[0] !== null) ? item.ProductName[0].value : item.ProductName[0];
                                } catch (ex) {
                                    projectId = item.ProductName[0];
                                }
                            }
                            else {
                                projectId = item.ProductName == null ? '' : item.ProductName.value;
                            }
                        } else {
