        FillDropdown: async function (type, refId) {
            var vm = this;
            vm.Loading= true;
            var search = '';
            var typeCode = type;
            if (type == "BillingCity" || type == "ShippingCity") {
                typeCode = 'STATE_DISTRICT';
            }
            else if (type == "BillingState" || type == "ShippingState") {
                typeCode = 'STATE';
            }
            else if (type == "ADDRESS_NAMES") {
                typeCode = 'ADDRESS_NAMES'
            }
            var json = [];
            if (refId != '') {
                var url = `id=&moduleName=${vm.$route.params.moduleName}&type=${typeCode}&refId=${refId}`;
               await LeadDataService.getDDLData(url).then(response => {
                vm.Loading= false;
                json = []
                    if (response.data != null) {
                        json = response.data.DATA;
                    }
                    if (json != null || typeof (json) != "undefined") {

                        if (type == "BillingCity") {
                            vm.Address.BillingAddress.BillingCity = '';
                            vm.Address.BillingAddress.BillingCityList = json;
                        }
                        else if (type == "BillingState") {
                            vm.Address.BillingAddress.BillingState = '';
                            vm.Address.BillingAddress.BillingStateList = json;
                        }
                        else if (type == "ShippingCity") {
                            vm.Address.ShippingAddress.ShippingCity = '';
                            vm.Address.ShippingAddress.ShippingCityList = json;
                        }
                        else if (type == "ShippingState") {
                            vm.Address.ShippingAddress.ShippingState = '';
                            vm.Address.ShippingAddress.ShippingStateList = json;
                        }
                        else if (type == "ADDRESS_NAMES") {
                            vm.Address.BillingAddress.BillingCompanyName = '';
                            vm.Address.BillingAddress.BillingCompanyNameList = json;
                            vm.Address.ShippingAddress.ShippingCompanyName = '';
                            vm.Address.ShippingAddress.ShippingCompanyNameList = json;
                        }
                        else if (type == "ACCOUNT_CONTACTS") {
                            vm.ShippingContactPerson = json
                            $.each(vm.LeadData, function (filedIndex, fieldItem) {
                                if (fieldItem.name == 'contact_id') {                                    
                                    fieldItem.select_options = json;
                                    if (ajaxRequestComplete) {
                                        fieldItem.value = '';
                                        fieldItem.select_value = '';
                                        vm.Address.ShippingAddress.ShippingContactPerson = '';
                                        vm.Address.BillingAddress.BillingContactPerson = '';
                                    }
                                }
                            });
                        }
                        else if (type == "ACCOUNT_DEALS") {
                            $.each(vm.LeadData, function (filedIndex, fieldItem) {
                                if (fieldItem.name == 'deal_id') {
                                    fieldItem.select_options = json;
                                }
                            });
                        }
                        else if (type == "ACCOUNT_CLIENTS") {
                            $.each(vm.LeadData, function (filedIndex, fieldItem) {
                                if (fieldItem.name == 'client_id') {
                                    fieldItem.select_options = json;
                                }
                            });
                        }
                    }
                    else {
                        vm.Address.BillingAddress.BillingCompanyName = 0;
                        vm.Address.BillingAddress.BillingCompanyNameList = json;
                    }

                });

            }
            else {
                if (type == "BillingCity") {
                    vm.Address.BillingAddress.BillingCity = '';
                    vm.Address.BillingAddress.BillingCityList = json;
                }
                else if (type == "BillingState") {
                    vm.Address.BillingAddress.BillingState = '';
                    vm.Address.BillingAddress.BillingStateList = json;
                }
                else if (type == "ShippingCity") {
                    vm.Address.ShippingAddress.ShippingCity = '';
                    vm.Address.ShippingAddress.ShippingCityList = json;
                }
                else if (type == "ShippingState") {
                    vm.Address.ShippingAddress.ShippingState = '';
                    vm.Address.ShippingAddress.ShippingStateList = json;
                }
                else if (type == "ADDRESS_NAMES") {
                    vm.Address.BillingAddress.BillingCompanyName = '';
                    vm.Address.BillingAddress.BillingCompanyNameList = json;
                }
            }
        },
        GetCountryData(search, type) {
            var url = `id=&moduleName=${this.$route.params.moduleName}&type=${type}`;
            var vm = this;
            LeadDataService.getDDLData(url + "&search=" + escape(search)).then(response => {
                var json = response.data;
                var UnitedStateVAl = json.DATA.filter(v => v.name == "United States");

                if (json != null) {
                    vm.CountryData = json.DATA;
                    var id = vm.quoteID;
                    if (UnitedStateVAl != null && UnitedStateVAl.length > 0 && id == "0") {
                        //vm.FillDropdown("ShippingState", UnitedStateVAl[0].value);
                        vm.FillDropdown("BillingState", UnitedStateVAl[0].value);
                        //vm.Address.ShippingAddress.ShippingCountry = UnitedStateVAl[0].value;
                        vm.Address.BillingAddress.BillingCountry = UnitedStateVAl[0].value;
                    }
                }
            });
            url = `id=&moduleName=${this.$route.params.moduleName}&type=ACCOUNTS`;
            LeadDataService.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    var json = response.data;
                    if (json != null) {
                        vm.AccountData = json.DATA;
                    }
                });
        },
