                                                    <div class="position-absolute panelcoming paneltax panelfix bg-light listing border-0 p-0 bg-white"
                                                        v-if='item.toggleTax' v-click-outside="outside">
                                                        <div class="col-12 float-left p-0">
                                                            <div class="p-0 border-0"><a href="javascript:;"
                                                                    class="close pt-0"
                                                                    style="font-size:23px; right: -20px; top: 10px;"
                                                                    data-dismiss="alert"
                                                                    v-on:click="ShowTax(item)">&times;</a>
                                                            </div>
                                                            <div class="table-responsive">
                                                                <table id="example" class="table-responsive border-0" aria-describedby="content">
                                                                    <thead class="thead-bg">
                                                                        <tr>
                                                                             <th scope="col" width="150px"><span>{{$t('Tax')}}</span>
                                                                            </th>
                                                                             <th scope="col" width="150px">
                                                                                <span>{{$t('TaxFor')}}</span>
                                                                            </th>
                                                                             <th scope="col" width="150px">
                                                                                <span>{{$t('Rate')}}</span></th>
                                                                             <th scope="col" class="text-center"><a
                                                                                    href="javascript:;"
                                                                                    v-on:click="AddMoreTax(item)"><em                                                    class="fa fa-plus-circle text-success"
                                                                                        style="font-size:20px;"></em></a>
                                                                            </th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        <tr v-for="(taxItem,Index) in item.TaxData"
                                                                            :key="Index">
                                                                            <td>
                                                                                <span
                                                                                    v-if="taxItem.TAX_ID>0">{{taxItem.TAX_NAME}}</span>
                                                                                <input type="text"
                                                                                    v-model="taxItem.TAX_NAME"
                                                                                    v-bind:class="{'form-control':true, 'is-invalid':isTaxNameFilled}"
                                                                                    v-if="taxItem.TAX_ID<=0"
                                                                                    @input="clearRateIfNameEmpty(taxItem, 'TAX', true)" />
                                                                            </td>
                                                                            <td>{{taxItem.TAX_FOR}}</td>
                                                                            <td>
                                                                                <div class="input-group">
                                                                                    <input type="text"
                                                                                        v-model="taxItem.TAX_RATE"
                                                                                        class="form-control col-9 float-left"
                                                                                        @input="onlyDecimal(taxItem, 'TAX', true)" />
                                                                                    <div class="input-group-append">
                                                                                        <span
                                                                                            v-if="taxItem.TAX_TYPE=='percent'"
                                                                                            class="input-group-text">%</span>
                                                                                    </div>
                                                                                </div>
                                                                            </td>
                                                                            <td class="text-center"
                                                                                v-if="taxItem.TAX_ID<=0">
                                                                                <a href="javascript:;"
                                                                                    v-on:click="DeleteMoreTax(item,Index)"><em                                                    class="fa fa-trash-o text-danger"
                                                                                        style="font-size:20px;"></em></a>
                                                                            </td>
                                                                        </tr>
                                                                        <tr
                                                                            v-if="item.TaxData == null || item.TaxData.length==0">
                                                                            <td colspan="4"
                                                                                class="text-danger text-center no-record">
                                                                                {{$t('NoRecordfound')}}</td>
                                                                        </tr>
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td><span>{{currency}} {{totalSumlineItem(item) | currency}}</span></td>
                                                <td class="text-center single-action" data-width="50">
                                                    <a href="javascript:;"
                                                        v-on:click="DeleteProductSummaryItem(item, index)"><em class="fa fa-trash action-icon text-danger"></em></a>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td colspan="7"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('SubTotal')}}:</td>
                                                <td colspan="2" class="border-bottom-0 align-middle">
                                                    <span>{{ProductSummary.SubTotal }}</span>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td colspan="7"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('ShippingCharges')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><input class="form-control"
                                                        type="text" placeholder="0.00"
                                                        v-model="ProductSummary.ShippingCharges"></td>
                                            </tr>
                                            <tr>
                                                <td colspan="7"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('Adjustments')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><input class="form-control"
                                                        type="text" placeholder="0.00"
                                                        v-model="ProductSummary.Adjustments"></td>
                                            </tr>

                                            <tr>
                                                <td colspan="7"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('Total')}} :</td>
                                                <td colspan="2" class="border-bottom-0"><span
                                                        v-bind:value="summaryTotal">{{ProductSummary.SummaryTotal}}</span>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>

           <div class="row flex-row-reverse mt-2">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" 
                                    v-bind:class="{disabled: submited}" 
                                    class="btn btn-warning mr-2" v-if="statusId != 1001" v-on:click="SaveAsDraft( valid,errors,'D')" 
                                    :title="$t('SaveAsDraft')"><em class="fa fa-save pr-2"></em>{{$t('SaveAsDraft')}}</a>
                                <a href="javascript:;"
                                    data-tour="step: 1; title: enter  dealDetail; content: AddVendorDetailContent"
                                    v-bind:class="{disabled: submited}" v-on:click="SubmitData( valid,errors,'A')"
                                    class="btn btn-success mr-2 addQuote_a" title="Save">
                                    <em class="fa fa-save pr-2"></em>{{$t('Save')}}
                                </a>
                                <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="onCancel">
                                    <em class="fa fa-close pr-2"></em>{{$t('Cancel')}}
                                </a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <small class="text-danger">{{$t('MandatoryString')}}</small>
                                </div>
                            </div>
                        </div>
                    </Form>
                    <modalpricebook v-if="showPricebookModal" :selectedItemIndex="selectedItemIndex" :summaryData="ProductSummary.SummaryData" 
                    @close="showPricebookModal = false" @SetPriceBook="SetPriceBook"></modalpricebook>
                </div>
            </div>
        </section>    

</template>